body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;

}
body{
	background-image: url('https://media.istockphoto.com/id/1294603953/vector/abstract-black-stripes-diagonal-background.jpg?b=1&s=612x612&w=0&k=20&c=67zOlXF2xg7oj18aOSZ5V2OJVRPcKg2ngwQhFxOSu10=');
	background-repeat: no-repeat;
	background-size: 100%;
	background-attachment: fixed;
}

@media (max-width: 1355px){
body{
   background-image: url('https://i.pinimg.com/originals/5a/3e/9b/5a3e9be10d39a01f23f8362eaafe3eb6.jpg');
   background-repeat: repeat;
}
}



@media (max-width: 579px){
.menu-bars {
  text-align: center;
  margin-left: 1rem;
  font-size: 1.8rem;
  background: none;
  position: fixed;
  z-index: 999;
}
.header-bars{
  display: none;
}
.default-nav{
  height: 60px;
  width: 100%;
  background-color: #0E0E0E;
  position: fixed;
  z-index: 998;
}

.default-nav2{
  height: 60px;
  width: 100%;
}

#svg{
  margin-top: 5px;
}

#side-nav-key.active{
  background-color: #50D4DD;
}
.nav-text a:hover {
  font-size: 1.2rem; 
}
}



@media (min-width: 580px){
.navbar-expand-lg .navbar-collapse {
  display: flex!important;
  justify-content: center;
  background-color: #0E0E0E;
  height: 80px;
  width: 100%;
  position: fixed;
  z-index: 999;
}

.navbar-collapse a.active{
  color: white;
  font-weight: bold;
  border-bottom: 1px solid gold;
}

.default-nav{
  height: 80px;
  width: 100%;
}
.default-nav2{
  display: none;
}

.menu-bars {
  display: none;
}
}



.nav-menu {
  background-color: #060b26;
  width: 200px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  z-index: 999;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: white;
  font-size: 18px;
  font-family: 'Raleway', sans-serif;
  width: 100%;
  height: 100%;
  padding: 0px 0px 0px 10px;
  display: flex;
  margin-right: 10px;
  align-items: center;
  border-radius: 4px;
}

.navbar-collapse a:hover{
  color: white;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}


.header-bars{
  font-size: 19px;
  text-decoration: none;
  color:white;
  font-family: 'Raleway', sans-serif;
}




*{
    color: white;
}
#education,#personal_skills,#programming_skills,#certification,#personal_achievement,#work_experience{
    font-size: 1.9rem;
